<template>
  <div>

    <b-form @submit.prevent="onSubmit">
      <b-form-group>
        <label for="paciente">Datos personales</label>
        <b-form-input
          type="text"
          readonly
        />
      </b-form-group>
      <b-form-group>
        <label for="celular">Celular para contacto</label>
        <b-form-input
          v-model="citaData.celular"
          type="text"
        />
      </b-form-group>
      <b-form-group>
        <label for="motivo">Motivo:</label>
        <b-form-textarea
          id="motivo"
          v-model="citaData.motivo"
          placeholder="Describir motivo..."
          rows="3"
          max-rows="6"
        />
      </b-form-group>
      <b-form-group
        label="Fecha de la cita"
        label-for="birth-date"
      >
        <flat-pickr
          :value="null"
          class="form-control"
          :config="{ locale: Spanish, enableTime: true, time_24hr: true, static: true, altInput: true, altFormat: 'l, d \\de F \\de Y \\Hora: h:i K', dateFormat: 'Y-m-d', dateFormat: 'Y-m-d h:i K'}"
        />
      </b-form-group>
      <b-form-group
        label="Servicio"
        label-for="nested-fecha-nacimiento"
      >
        <v-select
          v-model="selected"
          :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
          label="title"
          :options="[]"
          placeholder="Seleccionar"
        />
      </b-form-group>
      <b-form-checkbox
        checked="true"
        name="check-button"
        switch
        inline
      >
        Aprobar
      </b-form-checkbox>
    </b-form>
    <b-row class="mt-1">
      <b-col>
        <b-button
          variant="primary"
          class="mb-1 mb-sm-0 mr-0 mr-sm-1"
          :block="$store.getters['app/currentBreakPoint'] === 'xs'"
        >
          Guardar
        </b-button>
        <b-button
          variant="outline-secondary"
          :block="$store.getters['app/currentBreakPoint'] === 'xs'"
        >
          Cancelar
        </b-button>
      </b-col>
    </b-row>
  </div>

</template>

<script>
import {
  BRow, BCol, BForm, BFormGroup, BFormInput, BFormTextarea, BButton, BFormCheckbox,
} from 'bootstrap-vue'
import flatPickr from 'vue-flatpickr-component'
// eslint-disable-next-line import/extensions
// eslint-disable-next-line import/no-extraneous-dependencies
import { Spanish } from 'flatpickr/dist/l10n/es'
import vSelect from 'vue-select'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import { ref } from '@vue/composition-api'

export default {
  components: {
    BRow,
    BCol,
    BForm,
    BFormGroup,
    BFormInput,
    BFormTextarea,
    BButton,
    BFormCheckbox,

    // Externos
    flatPickr,
    vSelect,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      Spanish,
      selected: null,
    }
  },
  setup() {
    const blankCitaData = {
      id: null,
      celular: null,
      fecha_cita: null,
      servicio_id: null,
      motivo: null,
      estado: null,
    }

    const citaData = ref(JSON.parse(JSON.stringify(blankCitaData)))

    const onSubmit = () => {
      store.dispatch('citas/addCita', citaData.value)
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          if (error.response.status === 404) {
            console.log(error)
          }
        })
    }
    return {
      citaData,
      onSubmit,
    }
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
@import '@core/scss/vue/libs/vue-select.scss';
</style>
